import { OnInit, Component, AfterViewInit, ContentChildDecorator} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';


import {Pdf,ServicioService}from './../services/servicio.service';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit, AfterViewInit{
// Funcionalidades
show= false;
arrBangs:any[0];
arrBangsID:any[0];
eliminarV:any;
eliminarB=false;
ver:any;
contIdeas:any;
vioS=false;vioL=false;vioG=false;vioT=false;info=false;
objaux:Pdf;
pdf:Pdf={
  id :'',
  num:'',
  nombre:'',
  ambiente:'',
  factor:'',
  personaje:'',
  solitario:true,
  solido:'',
  gaseoso:'',
  liquido:'',
  P1:'',
  P2:'',
  P3_1:'',
  P3_2:'',
  P3_3:'',
  P3_4:'',
  tarjeta:'',
  tarjeta2:'',
  idea1:'',
  idea2:'',
  idea3:'',
  idea4:'',
  ideaselecionada:'',
  aprendizaje:'',
  V:'',
  C:'',
  R:'',
  P:'',
  tipo:'',
  img:''
};
liquido; solido; gaseoso = false;
section :string;
imgAmbiente = {
  empresarial: 'EMPRESARIAL',
  familiar: 'FAMILIAR',
  social: 'SOCIAL'
 }
 constructor(
  private serv:ServicioService,
  private route: ActivatedRoute,
  private router:Router
) { 
}
async ngOnInit(
){
  this.arrBangs=[];
  this.serv.deleteItem();
  this.section ='home';
  this.serv.verify=false;
  this.mipromesa();

}

ngAfterViewInit(){
  this.route.params.subscribe(params => {
    const _params = params.section;   
    this.section = _params;
  })
}
async ideas(pdf:Pdf){
   const cantidad =(await this.serv.consultarid(pdf)).subscribe(res=>
    {
      if(this.pdf.num==""){
        this.pdf.num = res.length+1;
      this.serv.AddItem(this.pdf);
      }
    }
    );
}
async getAllIdeas(pdf:Pdf){
  let contenedor
  const ideas =await this.serv.consultar(pdf).then(res=>{
    contenedor=res;
    this.contIdeas=contenedor;
    
  }).then(()=>{
    this.organizador();
  });
}
getBang: Observable<Array<any>>
async organizador(){
  await this.arrBangs[this.contIdeas.length];
  for(let a=0;a<this.contIdeas.length;a++){
    this.arrBangs[a]= await this.contIdeas[a].data().P1;
    
  }
  this.getBang=this.arrBangs;
}
async deleteBang(n){
  let almacenador= await this.contIdeas[n].data();
 this.serv.eliminar(almacenador.id, almacenador.num);
}
guardarEliminacion(i){
this.eliminarV=i;
}
eliminarN(){
  this.deleteBang(this.eliminarV);
}
eliminar(){
  this.serv.deleteItem();
}
async BangSeleccionado(item){
  let almacenador= await this.contIdeas[item].data();
  await this.serv.addItem(this.contIdeas[item].data());
  this.router.navigateByUrl('/pdf');
 }
openPopUP(item){
  switch(item){
    case 'eliminar':
      this.eliminarB=!this.eliminarB;
        break;
    case 'info':
      this.info=!this.info;
        break;
    case 'solido':
      this.solido=!this.solido;
      this.vioS=true;
        break;
    case 'liquido':
      this.liquido=!this.liquido;
      this.vioL=true;
        break;
    case 'gaseoso':
      this.gaseoso=!this.gaseoso;
      this.vioG=true;
        break;
    default:
        break;
  }
  this.show = !this.show;
  if((this.vioS==true)&&(this.vioL==true)&&(this.vioG==true)){
    this.vioT=true;
  }
}
 async startsession(){
   this.serv.deleteItem();
    this.serv.loginGoogle().finally(()=>{
      this.union();
    }).then(()=>{
      this.pdf.id=this.serv.email;
      this.pdf.nombre=this.serv.nombre;
    });
}
public avanzar(section){
  this.section = section;
}
async union(){
  if(this.serv.verify!==false){
    await this.avanzar('antes');
  }
}
goToIdeas(){
  this.router.navigateByUrl('/home/home');
}
cuantasPersonas(personas){
  console.log(personas);
}

switchAmbienteImg(ambiente){
  this.imgAmbiente[ambiente] = this.imgAmbiente[ambiente] == 
  this.imgAmbiente[ambiente].split('-')[0] ? `${this.imgAmbiente[ambiente]}-2`:  this.imgAmbiente[ambiente].split('-')[0];
}
datos(){
  this.ideas(this.pdf);
  
}
guardar(item){
  this.ideas(this.pdf);
  this.pdf.ambiente=item;
  this.avanzar('home');
  this.serv.AddItem(this.pdf);
}
alLector(){
  this.router.navigateByUrl('scanner');
}
gotoSolido(){
  this.router.navigateByUrl('/solido/comenzar/0');
}
async mipromesa(){
  new Promise((resolve) => {
    this.objaux = JSON.parse(this.serv.getItem('_cap_pdf2'));
   if(this.objaux.num!=""){
     this.pdf=  this.objaux; 
     return resolve(this.pdf);
   }
 });
}
}
